#projects
{
    margin: 0 auto;
    width: 100 vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.projectpagetitle{
    margin: 1rem 0;
    font-size: 3rem;

}
.projectpagesubtitle{
    margin: 1rem 0;
    font-size: 1.5rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    width:100vw;
    max-width: 80%;
    text-align: left;
    background: rgb(0, 255, 255)
}

.projectpagesubtitle:hover{
    margin: 1rem 0;
    font-size: 1.5rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    width:100vw;
    max-width: 80%;
    text-align: left;
    background: rgb(220, 20, 60)
}